.profileInfo_socialicons1__1Aa2D {
    display: inline-block;
    list-style: none;
}

.profileInfo_socialicons1__1Aa2D li {
    display: inline-block;
    position: relative;
    font-size: 24px;
    margin: 5px;
}

.profileInfo_socialicons1__1Aa2D img {
    color: #fff;
    position: absolute;
    top: 18px;
    left: 18px;
    width: 24px;
    height: 24px;
    text-align: center;
    transition: all 0.3s ease-out;
}

.profileInfo_socialicons1__1Aa2D a:before {
    -webkit-transform: scale(1);
            transform: scale(1);
    content: "";
    width: 60px;
    height: 60px;
    border-radius: 100%;
    display: block;
    background: linear-gradient(45deg, yellow, orange);
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2), 0 2px 4px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease-out;
}

.profileInfo_socialicons1__1Aa2D a:hover:before {
    -webkit-transform: scale(0);
            transform: scale(0);
    transition: all 0.3s ease-in;
}

.profileInfo_socialicons1__1Aa2D a:hover img {
    -webkit-transform: scale(1.8);
            transform: scale(1.8);
    color: yellow;
    transition: all 0.3s ease-in;
}
